<template>
    <div>
        <div class="row align-items-center">
            <div class="col-4">
                <img src="@/assets/logo.png" width="150px" height="160px" v-bind:alt="title"/>
            </div>
            <div class="col-8">
                <div>{{ author }} - {{ title }}</div>
                <div>ISBN：{{ isbn }}</div>
                <div>单价：￥{{ price }}</div>
                <div v-bind:class="{'text-secondary':stock<=0}">库存剩余：{{ stock }}</div>
            </div>
        </div>
        <button v-if="admin" class="btn btn-block btn-outline-info col-10 offset-1 mt-3">修改图书信息</button>
        <div class="h4 text-left mt-5">评论</div>
        <hr/>
        <!-- Comments on the book -->
        <div v-if="hasComments">
            <div v-for="(comminfo, index) in comments" v-bind:key="index" class="text-left">
                {{ comminfo.username }}说：{{ comminfo.comment }}
                <hr/>
            </div>
        </div>
        <div v-else>暂无评论；来写下第一条评论吧</div>
        <div class="row ml-0 mr-0 mt-4">
            <input type="text" class="form-control col-10" placeholder="留下你的评论..."
                   v-model="intendedComment"/>
            <button class="btn btn-outline-primary col-2">评论</button>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'BookDetails',
        props: ['title', 'author', 'isbn', 'price', 'stock', 'comments', 'admin'],
        computed: {
            hasComments: function () {
                return this.comments.length > 0;
            }
        },
        data: function () {
            return {
                intendedComment: ''
            };
        }
    }
</script>

<style scoped>

</style>
