<template>
    <div class="row align-items-center">
        <div class="col-2">
            <img src="@/assets/logo.png" width="150px" height="160px" v-bind:alt="title"/>
        </div>
        <div class="col-6">
            <div class="text-center">
                <div>{{ author }} - {{ title }}</div>
                <div>ISBN：{{ book.isbn }}</div>
                <div v-if="noStock" class="text-secondary">(-_-;) 没库存了</div>
                <div v-else>库存剩余：{{ book.stock }}</div>
            </div>
        </div>
        <div class="col-2">
            <!-- will be rewritten with AJAX -->
            <button class="btn btn-outline-info w-100 mb-2" v-on:click="showDetails()">详情</button>
            <button v-if="admin" class="btn btn-outline-success w-100">库存</button>
            <button v-if="!admin" class="btn btn-outline-fav w-100">收藏</button>
        </div>
        <div class="col-2">
            <button v-if="!admin" class="btn btn-outline-primary w-100" v-bind:disabled="noStock"
            v-bind:title="noStock?'库存为空，去看看其他书吧':''">
                添加到购物车
                <br/>￥{{ book.price }}
            </button>
            <button v-if="admin" class="btn btn-outline-danger w-100">删除</button>
        </div>
        <!-- Detail & mask -->
        <div v-if="detailed">
            <div class="mask"></div>
            <div class="on-mask bg-white window-md p-4">
                <button class="btn btn-outline-danger col-2 offset-10 mb-3"
                        v-on:click="hideDetails()">关闭</button>
                <BookDetails v-bind:title="title" v-bind:author="author" v-bind:isbn="book.isbn"
                             v-bind:stock="book.stock" v-bind:price="book.price" v-bind:admin="admin"
                             v-bind:comments="bookComments"></BookDetails>
            </div>
        </div>
    </div>
</template>

<script>
    import BookDetails from '@/components/BookDetails';
    export default {
        name: 'BookListItem',
        components: {BookDetails},
        props: ['book', 'admin'],
        computed: {
            noStock: function () {
                return this.book.stock <= 0;
            },
            author: function () {
                if(this.book.author.length === 0){
                    return '佚名';
                }
                return this.book.author;
            },
            title: function () {
                if(this.book.title.length === 0){
                    return '无题';
                }
                return this.book.title;
            }
        },
        data: function () {
            return {
                detailed: false,
                bookComments: []
            };
        },
        methods: {
            showDetails: function() {
                this.detailed = true;
                // AJAX part, requesting for book comments
                this.bookComments = [
                    {username: 'Alice', comment: this.title + " is nice :)"},
                    {username: 'Kevin', comment: this.author + "写的书都还行"}
                    ];
            },
            hideDetails: function () {
                this.detailed = false;
            },
        }
    }
</script>

<style scoped>

</style>
