<template>
    <tr>
        <td>{{ order.date }}</td>
        <td>{{ order.buyerUid }}</td>
        <td>
            <div v-for="(book, index) in order.books" v-bind:key="index">
                ISBN：{{ book.isbn }}，单价：{{ book.price }}，购买数量：{{ book.count }}
            </div>
        </td>
        <td>￥{{ totalPrice }}</td>
    </tr>
</template>

<script>
    export default {
        name: 'OrderItem',
        props: ['order'],
        computed: {
            totalPrice: function () {
                var i = 0, sum = 0, items = this.order.books;
                for(i = 0; i < items.length; i++){
                    sum += items[i].price * items[i].count;
                }
                return sum;
            }
        }
    }
</script>

