<template>
    <div>
        <div v-for="(item, index) in items" v-bind:key="index">
            <CartItem v-bind:item="item"></CartItem>
        </div>
        <hr/>
        <!-- Total price & 'pay' button -->
        <div class="col-2 offset-10 text-primary h3 pt-3 pb-3">
            总计：￥{{ totalPrice }}
        </div>
        <hr/>
        <button class="btn btn-block btn-outline-success col-6 offset-3">买单！</button>
    </div>
</template>

<script>
    import CartItem from "@/components/CartItem";
    export default {
        name: 'CartContent',
        components: {CartItem},
        computed: {
            totalPrice: function () {
                var i = 0, sum = 0;
                for(i = 0; i < this.items.length; i++){
                    sum += this.items[i].price * this.items[i].count;
                }
                return sum;
            }
        },
        data: function () {
            return {
                items: [
                    {title: '泰戈尔诗选', author: '泰戈尔', isbn: '2', price: 20, count: 3},
                    {title: '深入了解计算机系统', author: 'Randal E. Bryant, David R. O\'Hallaron', isbn: '22', price: 100, count: 1}
                ]
            };
        }
    }
</script>

