<template>
    <div class="row align-items-center">
        <div class="col-2">
            <img src="@/assets/logo.png" width="150px" height="160px" v-bind:alt="title"/>
        </div>
        <div class="col-6">
            <div class="text-center">
                <div>{{ author }} - {{ title }}</div>
                <div>ISBN：{{ item.isbn }}</div>
                <div>单价：￥{{ item.price }}</div>
                <div>购买数量：{{ item.count }}</div>
            </div>
        </div>
        <div class="col-2">
            <button class="btn btn-outline-primary w-100 mb-2">修改数量</button>
            <button class="btn btn-outline-danger w-100">移出购物车</button>
        </div>
        <div class="col-2 text-primary h4">
            ￥{{ itemPrice }}
        </div>
    </div>
</template>

<script>
    export default {
        name: 'CartItem',
        props: ['item'],
        computed: {
            itemPrice: function () {
                return this.item.price * this.item.count;
            },
            author: function () {
                if(this.item.author.length === 0){
                    return '佚名';
                }
                return this.item.author;
            },
            title: function () {
                if(this.item.title.length === 0){
                    return '无题';
                }
                return this.item.title;
            }
        }
    }
</script>

