<template>
    <div>
        <!-- Filters -->
        <div>
            <div class="input-group bg-secondary mt-3 mb-3 pt-2 pb-2">
                <!-- Search by title -->
                <input type="text" class="form-control col-2 offset-9" placeholder="按书名搜索"
                       v-model="filters.title"/>
                <button class="btn btn-info col-1">查询</button>
            </div>
            <ul class="nav nav-tabs nav-justified mt-3 mb-3">
                <li class="nav-item" v-for="(ctg, index) in getCategories" v-bind:key="index"
                    v-on:click="switchCategory(index)" v-bind:class="categoryClass(index)">
                    <a>{{ ctg }}</a>
                </li>
            </ul>
        </div>
        <!-- ADMIN: Add new books -->
        <div v-if="admin&&isValidCategory">
            <button class="btn btn-block btn-outline-primary col-10 offset-1 mb-3">添加新的图书...</button>
            <hr/>
        </div>
        <!-- Books in the selected category -->
        <div>
            <span v-if="selectedCategory<0">（在上方选择一个分类）</span>
            <span v-else-if="isEmptyCategory">本分类暂无可供图书~</span>
            <div v-else>
                <BookListItem v-for="(book, index) in getBooks" v-bind:key="index"
                              v-bind:book="book" v-bind:admin="admin">
                </BookListItem>
            </div>
        </div>
    </div>
</template>

<script>
    import BookListItem from '@/components/BookListItem';
    export default {
        name: 'BookList',
        components: {BookListItem},
        props: {
            admin: {
                default: false
            }
        },
        computed: {
            getCategories: function () {
                var catas = [];
                var i;
                for(i = 0; i < this.library.length; i++){
                    catas[i] = this.library[i].category;
                }
                return catas;
            },
            getBooks: function () {
                var index = this.selectedCategory;
                if(index >= 0 && index < this.getCategories.length){
                    return this.library[index].books;
                }
                return [];
            },
            isEmptyCategory: function () {
                if(this.isValidCategory) {
                    return this.library[this.selectedCategory].books.length <= 0;
                }
                return true;
            },
            isValidCategory: function () {
                return this.selectedCategory >= 0 && this.selectedCategory < this.getCategories.length;
            }
        },
        methods: {
            categoryClass: function (index) {
                return (index === this.selectedCategory) ? 'nav-link active' : 'nav-link';
            },
            switchCategory: function (index) {
                if(index >= 0 && index < this.getCategories.length){
                    this.selectedCategory = index;
                }
            }
        },
        data: function () {
            // set temporarily with fixed data
            // ISBN as the primary key (unique identifier) of a book
            return {
                filters: {
                    title: ''
                },
                library: [
                    {
                        category: '文学作品',
                        books: [
                            {title: 'Macbeth', author: 'W. Shakespeare', image: '@/assets/logo.png', isbn: '1', stock: 32, price: 28},
                            {title: '泰戈尔诗选', author: '泰戈尔', image: '@/assets/logo.png', isbn: '2', stock: 30, price: 20},
                            {title: 'Hamlet', author: 'W. Shakespeare', image: '@/assets/logo.png', isbn: '3', stock: 35, price: 25},
                            {title: '狂人日记', author: '鲁迅', image: '@/assets/logo.png', isbn: '4', stock: 0, price: 15}
                        ]
                    },
                    {
                        category: '科技创新',
                        books: [
                            {title: 'Nature 2017', author: 'Nature', image: '@/assets/logo.png', isbn: '11', stock: 30, price: 5},
                            {title: 'JAMA 2017', author: 'JAMA', image: '@/assets/logo.png', isbn: '12', stock: 30, price: 5}
                        ]
                    },
                    {
                        category: '教科教辅',
                        books: [
                            {title: '理论力学', author: '', image: '@/assets/logo.png', isbn: '21', stock: 0, price: 35},
                            {title: '深入了解计算机系统', author: 'Randal E. Bryant, David R. O\'Hallaron', image: '@/assets/logo.png', isbn: '22', stock: 10, price: 100},
                            {title: 'C++ Primer Plus', author: 'Stephen Prata', image: '@/assets/logo.png', isbn: '23', stock: 5, price: 90},
                            {title: '电路基础', author: '', image: '@/assets/logo.png', isbn: '24', stock: 30, price: 65}
                        ]
                    },
                    {
                        category: '艺术欣赏',
                        books: [
                            {title: '世界名画家全集', author: '何政广', image: '@/assets/logo.png', isbn: '31', stock: 10, price: 50},
                            {title: '交响音乐通俗讲座', author: '薛金炎 李近朱', image: '@/assets/logo.png', isbn: '32', stock: 15, price: 20}
                        ]
                    },
                    {
                        category: '时尚期刊',
                        books: []
                    },
                    {
                        category: '其他读物',
                        books: [
                            {title: 'The Times', author: '', image: '@/assets/logo.png', isbn: '101', stock: 0, price: 15},
                            {title: 'The Economist', author: '', image: '@/assets/logo.png', isbn: '102', stock: 10, price: 16}
                        ]
                    }
                ],
                selectedCategory: -1
            };
        }
    }
</script>

<style scoped>

</style>
